<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process336"></a>

<p><br></p>
<p><span class="rvts93">总评：</span></p>
<p><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1685.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》是一款非常出色的美式战略</span><span class="rvts20">RPG</span><span class="rvts21">游戏，它最大的游戏性在于自由度非常高的</span><span class="rvts20">RPG</span><span class="rvts21">剧情以及灵活的技能选择和应用。</span>
<br><span class="rvts21">它打破了传统</span><span class="rvts20">RPG</span><span class="rvts21">中对于</span><span class="rvts20"></span><span class="rvts21">职业</span><span class="rvts20"></span><span class="rvts21">的严格划分概念，更多的倾向于玩家自己搭配学派技能和属性加点，这使得游戏的战斗充满了多样化和个性化。</span>
<br><span class="rvts21">即便是在同伴方面，原罪</span><span class="rvts20">2</span><span class="rvts21">也采取了独立支线的穿插手法，使得玩家可以探索更多</span><span class="rvts20">6</span><span class="rvts21">名源力者同伴身上的秘密和故事。</span>
<br><span class="rvts21">该游戏的最大剧情亮点在于它的剧情选择可能性较多，有些甚至会影响好几张地图，涉及多方面的阵营势力。该游戏的每个支线都是一个独特的亮点，这会比完成主线更加有趣。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1686.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">在图像方面，游戏的图像更倾向于冷色魔法风格，采取独特的细致近景描绘和模糊的远景搭配，可以使玩家身临其境，切实感受角色的真实处境。</span>
<br><span class="rvts21">音乐方面，各个角落都充满了大气蓬勃的古代帝国风格，外加一些神秘的魔法曲目，可以生动的描绘出一个神秘无比又暗藏权利斗争的神界。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1687.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">在独特的游戏世界观下，《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》为我们展现了神界的各个阶层生活态度以及不同种族的鲜明特征。我们可以从游戏中体会到傲慢的人类，狡诈的矮人，受偏见的精灵，自诩高贵的蜥蜴以及喜怒无常的不死族。</span>
<br><span class="rvts21">我们还可以深切体会到神界里下层市民的卑微与圆滑，贵族的蛮横和吹毛求疵。这是在其它</span><span class="rvts20">RPG</span><span class="rvts21">游戏中很少能够成功表达的鲜明游戏世界观。</span>
<br><span class="rvts21">最后，《神界</span><span class="rvts20">2</span><span class="rvts21">》是一款成功的</span><span class="rvts20">RPG</span><span class="rvts21">游戏。它的画面优秀，音乐美妙，剧情丰富，游戏自由度高，是一款非常值得推荐的，耐玩的好游戏！</span></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



